import React, { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

import { categoryOption } from './Option';
import './MyPage.css';

function MyPage(props) {
    const navigate = useNavigate();
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [region, setRegion] = useState("");
    const [dong, setDong] = useState("");
    const [category, setCategory] = useState("");
    const { postId } = useParams();

    const categoryOption = [
        {value: "drink", name: "-"},
        {value: "a", name: "A"},
        {value: "b", name: "B"},
        {value: "c", name: "C"},
        {value: "d", name: "D"},
    ];

    const findName = () => {
        fetch('${API}login', {
            method : "GET",
            headers : {
                "Content-Type" : "application/json",
                Authorization: localStorage.getItem('login-token')
            },
        })
        .then(response => {return response.json()})
        .then(response => setName(current => [...current, response.kakaoNickname]))
    }

    const findEmail = () => {
        fetch('/mypage/users/me', {
            method : "GET",
            headers : {"Content-Type" : "application/json"},
        })
        .then(response => {return response.json()})
        .then(response => {response.map((value)=>{
            setEmail(current => [...current, value.kakaoEmail]);
        })})
    }

    const handleChangeName = (event) => {
        setName(event.target.value);
    };

    const handleChangeEmail = (event) => {
        setEmail(event.target.value);
    };

    const handleChangePassword = (event) => {
        setPassword(event.target.value);
    };

    const handleChangeRegion = (event) => {
        setRegion(event.target.value);
    };

    const handleChangeDong = (event) => {
        setDong(event.target.value);
    };

    const handleChangeCategory = (event) => {
        setCategory(event.target.value);
    };

    const handleSubmit = (event) => {
        alert(` .`);
        //   
        event.preventDefault();
    };

    return (
        <form onSubmit={handleSubmit}>
            <div className="Wrapper">
                <div className="Container">
                    <div className="PostContainer">
                        <h3> </h3>
                        <div className="Label">
                            
                            <input style={{width: "180px", height: "30px"}} type="text" defaultValue={name} onChange={handleChangeName}/>
                        </div>
                        <div className="Label">
                              
                            <input style={{width: "180px", height: "30px"}} type="text" defaultValue={""} onChange={handleChangeEmail} />
                        </div>
                        <div className="Label">
                            
                            <input style={{width: "180px", height: "30px"}} type="password" defaultValue={""} onChange={handleChangePassword} />
                        </div>
                        <p></p>
                        <h3> </h3>
                        <div className="Label">
                              
                            <input style={{width: "180px", height: "30px"}} type="text" defaultValue={""} onChange={handleChangeRegion} />
                        </div>
                        <div className="Label">
                              
                            <input style={{width: "180px", height: "30px"}} type="text" defaultValue={""} onChange={handleChangeDong} />
                        </div>
                        <div className="Label">
                            
                            <select options={categoryOption} style={{width: "180px", height: "30px"}} defaultValue={"-"}>
                                {categoryOption.map((option) => (
                                    <option value={option.value}>{option.name}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                    <div className="PhotoContainer">
                        <div className="Photo"></div>
                        <div
                            className="Button"
                            type="submit"
                            title=" "
                            onClick={() => {navigate("/");}}
                        >
                             
                        </div>
                    </div> 
                </div>
                <div
                    className="Button"
                    type="submit"
                    title=" "
                    onClick={() => {navigate("/");}}
                >
                     
                </div>
            </div>
        </form>
    );
}

export default MyPage;